import axios from 'axios';

const baseurl = '/api/blogs';

const getAll = () => {
  const request = axios.get(baseurl);
  return request.then(response => response.data);
};

const deleteBlog = entryId => {
  const request = axios.delete(`${baseurl}/${entryId}`);
  return request.then(response => response.data);
};

const postBlog = newEntry => {
  const request = axios.post(baseurl, newEntry);
  return request.then(addedBlog => addedBlog.data);
};

const putBlog = (entryId, newBlog) => {
  const request = axios.put(`${baseurl}/${entryId}`, newBlog);
  return request.then(response => response.data);
};

export default { getAll, deleteBlog, postBlog, putBlog };
