import React from 'react';

const DisplayInput = ({ message, inputValue, onChangeHandler }) => {
  const divStyle = {
    margin: '8px'
  };

  return (
    <div style={divStyle}>
      {message}
      {':   '}
      <input value={inputValue} onChange={onChangeHandler} />
    </div>
  );
};

export default DisplayInput;
