import React from 'react';

const DisplayEntry = ({ title, author, url, likes, deleteHandler, likeHandler }) => {
  const divStyle = {
    border: '3px solid black',
    margin: '20px',
    padding: '0px 0px 0px 20px'
  };

  return (
    <div style={divStyle}>
      <p>Title: {title}</p>
      <p>Author: {author}</p>
      <p>
        Url: <a href={url}> {url} </a>
      </p>
      <p>Likes: {likes}</p>
      <button onClick={deleteHandler}>Delete</button>
      <button onClick={likeHandler}>Like!</button>
    </div>
  );
};

const DisplayEntries = ({ entries, onClickDeleteHandler, onClickLike }) => {
  const newEntries = entries.map(entry => (
    <DisplayEntry
      deleteHandler={() => onClickDeleteHandler(entry.id)}
      likeHandler={() => onClickLike(entry.id, entry.title, entry.author, entry.url, entry.likes)}
      key={entry.id}
      title={entry.title}
      author={entry.author}
      url={entry.url}
      likes={entry.likes}
    />
  ));
  return <div>{newEntries}</div>;
};

export default DisplayEntries;
