import React, { useState, useEffect } from 'react';
import DisplayEntries from './DisplayEntries';
import blogService from '../services/blogService';
import DisplayInput from './DisplayInput';

const App = () => {
  const [newBlogs, setBlogs] = useState([]);
  const [newTitle, setTitle] = useState('');
  const [newAuthor, setAuthor] = useState('');
  const [newUrl, setUrl] = useState('');

  const titleChangeHandler = event => setTitle(event.target.value);
  const authorChangeHandler = event => setAuthor(event.target.value);
  const urlChangeHandler = event => setUrl(event.target.value);

  const deleteHandler = entryId => {
    blogService.deleteBlog(entryId).then(response => {
      blogService.getAll().then(blogEntries => {
        setBlogs([...blogEntries]);
      });
    });
  };

  const likeHandler = (entryId, title, author, url, likes) => {
    const replacedBlog = {
      id: entryId,
      title,
      author,
      url,
      likes: likes + 1
    };

    blogService.putBlog(entryId, replacedBlog).then(response => {
      blogService.getAll().then(blogEntries => {
        setBlogs([...blogEntries]);
      });
    });
  };

  // Loads the blog json file
  useEffect(() => {
    blogService.getAll().then(blogDatabase => {
      console.log(blogDatabase);
      setBlogs([...blogDatabase])});
  }, []);

  const submitHandler = event => {
    event.preventDefault();
    const newEntry = {
      title: newTitle,
      author: newAuthor,
      url: newUrl,
      likes: 0,
      id: newBlogs.length + 1
    };
    blogService.postBlog(newEntry).then(response => {
      setTitle('');
      setAuthor('');
      setUrl('');
      setBlogs(newBlogs.concat(response));
    });
  };

  return (
    <div>
      <h2>Create blog: </h2>
      <form onSubmit={submitHandler}>
        <DisplayInput
          message="Title"
          onChangeHandler={titleChangeHandler}
          inputValue={newTitle}
        />
        <DisplayInput
          message="Author"
          onChangeHandler={authorChangeHandler}
          inputValue={newAuthor}
        />
        <DisplayInput
          message="Url"
          onChangeHandler={urlChangeHandler}
          inputValue={newUrl}
        />
        <div>
          <button type="submit">Add blog</button>
        </div>
      </form>
      <h2>Blogs: </h2>
      <DisplayEntries
        entries={newBlogs}
        onClickDeleteHandler={deleteHandler}
        onClickLike={likeHandler}
      />
    </div>
  );
};

export default App;
